
.scrollTop {
    position: fixed; 
    width: 5%;
    bottom: 100px;
    right: 10px;
    align-items: flex-end;
    height: 20px;
    justify-content: end;
    z-index: 1000;
    cursor: pointer;
    animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
    color: #6C63FF;
    margin-right: 80px;
  }
  
  .scrollTop:hover{
    opacity: 1;
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
